
.fade-enter-active, .fade-leave-active {
  transition: visibility 15s;
}
.fade-enter-active .panel__meta-data--left,
  .fade-enter-active .panel__meta-data--left:after,
  .fade-enter-active .panel__title,
  .fade-enter-active .panel__desc,
  .fade-enter-active .scroll-indicator, .fade-leave-active .panel__meta-data--left,
  .fade-leave-active .panel__meta-data--left:after,
  .fade-leave-active .panel__title,
  .fade-leave-active .panel__desc,
  .fade-leave-active .scroll-indicator {
    transition: transform 0.3s cubic-bezier(0.39, 0.575, 0.565, 1), opacity 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.fade-enter-active .panel__meta-data--left {
  transition-delay: 0;
}
.fade-enter-active .panel__meta-data--left:after {
  transition-delay: 100ms;
}
.fade-enter-active .panel__title {
  transition-delay: 175ms;
}
.fade-enter-active .panel__desc {
  transition-delay: 225ms;
}
.fade-enter-active .scroll-indicator {
  transition-delay: 325ms;
}
.fade-enter, .fade-leave-to {
  visibility: visible;
}
.fade-enter .panel__meta-data--left,
  .fade-enter .panel__meta-data--left:after,
  .fade-enter .panel__title,
  .fade-enter .panel__desc, .fade-leave-to .panel__meta-data--left,
  .fade-leave-to .panel__meta-data--left:after,
  .fade-leave-to .panel__title,
  .fade-leave-to .panel__desc {
    opacity: 0;
    transform: translateY(40px);
}
.fade-enter .scroll-indicator, .fade-leave-to .scroll-indicator {
    opacity: 0;
    transform: translateX(40px);
}
.scroll-indicator-wrapper {
  position: absolute;
  transform: translateX(-50%) rotate(90deg);
  left: 50%;
  top: -10px;
  z-index: 2;
}
.scroll-indicator {
  font-family: 'Roboto';
  color: white;
  font-weight: 500;
  font-size: 0.9em;
  position: relative;
  display: inline-block;
}
.scroll-indicator:after {
    content: '';
    display: inline-block;
    width: 150px;
    height: 3px;
    background: #dba54b;
    vertical-align: middle;
    margin-left: 15px;
    margin-top: -3px;
}
.post__header {
  height: 100vh;
  overflow: hidden;
}
.header__content {
  position: absolute;
  bottom: 5vw;
  left: 5vw;
  z-index: 3;
}
.post__header img {
  width: 100vw;
  position: absolute;
  transform: scale(1.2);
}
.post__sidebar {
  font-family: 'Eczar';
  position: absolute;
  left: -5vw;
  top: 8vw;
  line-height: 1.65;
  letter-spacing: 0.05em;
  transform-origin: top left;
  transform: rotate(90deg);
}
.post__sidebar:before {
    content: '';
    display: inline-block;
    width: 50px;
    height: 2px;
    background: #dba54b;
    vertical-align: middle;
    margin-right: 15px;
    margin-top: -3px;
}
.post__category {
  font-weight: 500;
  margin-right: 15px;
}
.post__body {
  max-width: 1000px;
  position: relative;
  margin: 0 auto;
