  padding-top: 8vw;
  padding-bottom: 8vw;
}
.post__content p {
  max-width: 60%;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Eczar';
  font-size: 1.35em;
  line-height: 1.65;
}
.post__content img {
  max-width: 100%;
  display: block;
}
.post__content * + * {
  margin-top: 4vw;
}



