
.page-title {
  position: absolute;
  top: 1em;
  left: 1em;
  z-index: 10;
  font-family: 'Eczar';
  color: white;
  font-size: 20px;
  letter-spacing: 0.1em;
  font-weight: 400;
}
.panel.transition-out {
  transition: width 0.3s 0.4s cubic-bezier(0.39, 0.575, 0.565, 1), min-width 0.3s 0.4s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.panel.transition-out:after {
    transform: translateX(101%);
    background: white;
    z-index: 100;
    opacity: 1;
}
.panel.hide:after {
  transition: transform 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
  transform: translateX(0%);
}
.panel.hide {
  width: 0%;
  min-width: 0%;
}
.panel.open .panel__content {
  opacity: 0;
  transform: translateY(100%);
}
.panel.open {
  width: 100% !important;
}
.panels {
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
}
.panel__content {
  position: relative;
  z-index: 3;
  transform: translateY(75%);
  transition: transform 0.35s cubic-bezier(0.39, 0.575, 0.565, 1), opacity 0.35s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.panel__title,
.panel__desc,
.panel__meta-data,
.panel__meta-data--left {
  max-width: 450px;
  margin: 0 auto;
}
.panel__desc,
.panel__meta-data,
.panel__meta-data--left {
  font-family: 'Roboto';
  font-weight: 300;
  color: white;
  letter-spacing: 0.045em;
  line-height: 1.6;
}
.panel__category {
  font-weight: 500;
}
.panel__meta-data, .panel__meta-data--left {
  position: relative;
  padding-top: 1em;
}
.panel__meta-data:before, .panel__meta-data--left:before {
    content: '';
    top: 0;
    position: absolute;
    width: 3em;
    left: 50%;
    transform: translateX(-50%);
    height: 3px;
    background: #dba54b;
}
.panel__meta-data--left:before {
  left: 0;
  transform: translatex(0);
}
.panel__title {
  margin-top: 0.5em;
  margin-bottom: 1em;
  font-family: 'Eczar';
  font-weight: 500;
  font-size: 42px;
  letter-spacing: 0.05em;
  line-height: 1.2;
  color: #ffffff;
}
.panel__background {
  position: absolute;
  z-index: -2;
  width: 100vw;
  top: 0;
  transition: 0.5s 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.panel.open .panel__background {
  transform: scale(1.2);
}
.panel:nth-child(1) .panel__background {
  left: 0;
}
.panel:nth-child(2) .panel__background {
  left: 50%;
  transform: translateX(-50%);
}
.panel:nth-child(3) .panel__background {
  right: 0;
}
.panel {
  width: 33.333%;
  min-width: 550px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: width 1s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.panel:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: opacity 0.3s ease;
    z-index: -1;
}
.panel:hover {
    width: 100%;
    cursor: pointer;
}
.overlay {
  position: relative;
}
.overlay:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    transition: opacity 0.3s ease;
    background: linear-gradient(to bottom, transparent, rgba(30, 30, 30, 0.6));
}



